@use '../../styles/var.scss' as var;

    .overlay {
    z-index: 10;
    display: none;
    background: rgba(0, 0, 0, .6);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    
    &.open {
        display: block;
    }

}
.child {
    
    overflow-y: scroll;
    z-index: 10;
    display: none;
    position: fixed;
    top: 5vh;
    left: 5%;
    width: 90%;
    height: 90vh;
    border-radius: 4px;
    background: #fff;
    padding: 20px;

    &.open {
        display: block;
    }
    .closeButton {
        text-align: right;
    }

    @include var.mq(calc(var.$tab + var.$tab / 10)) {
        width: var.$tab;
        left: calc((100% - var.$tab) / 2);
    }

}